function list(state = [], action) {
  switch (action.type) {
    case "RANK_1": {
      let findIndexOfExisting = state.findIndex(
        (dish) => dish.type === action.type
      );
      let findDish = state.find((dish) => dish.type === action.payload.type);
      return findIndexOfExisting >= 0
        ? [...state]
        : [
            ...state,
            { ...action.payload, type: "RANK_1", rank: "Rank 1", point: 30 },
          ];
    }
    case "RANK_2": {
      let findIndexOfExisting = state.findIndex(
        (dish) => dish.type === action.type
      );
      console.log(findIndexOfExisting);
      return findIndexOfExisting >= 0
        ? [...state]
        : [
            ...state,
            { ...action.payload, type: "RANK_2", rank: "Rank 2", point: 20 },
          ];
    }
    case "RANK_3": {
      let findIndexOfExisting = state.findIndex(
        (dish) => dish.type === action.type
      );
      console.log(findIndexOfExisting);
      return findIndexOfExisting >= 0
        ? [...state]
        : [
            ...state,
            { ...action.payload, type: "RANK_3", rank: "Rank 3", point: 10 },
          ];
    }
    case "CLEAR_ALL":
      return [];
    default:
      return state;
  }
}
export default list;
