const credentials = [
  {
    id: 1,
    username: "amar",
    password: "amar123",
  },
  {
    id: 2,
    username: "akbar",
    password: "akbar123",
  },
  {
    id: 3,
    username: "antony",
    password: "antony123",
  },
  {
    id: 4,
    username: "john",
    password: "john123",
  },
  {
    id: 5,
    username: "paul",
    password: "paul123",
  },
];

export const signIn = (userData, navigate) => {
  const ifUserNameExist = credentials.find(
    (user) => user.username == userData.username
  );
  if (ifUserNameExist !== undefined) {
    if (ifUserNameExist.password == userData.password) {
      localStorage.setItem("user", JSON.stringify(ifUserNameExist));
      navigate("/");
    } else {
      alert("Password is not correct...");
    }
  } else {
    alert("User Does not exist....");
  }
};

export const signOut = () => {
  localStorage.clear();
};
