import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Button, Checkbox, Typography, Box } from "@mui/material";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Container } from "@mui/system";
import { Link, Navigate, useNavigate } from "react-router-dom";

function Datatable() {
  let data = useSelector((state) => state.dish);
  let ifSelected = useSelector((state) => state.list);
  const [clicked1, setClicked1] = useState([]);
  const [clicked2, setClicked2] = useState([]);
  const [clicked3, setClicked3] = useState([]);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  function setAllStates() {
    setClicked1(Array(data.length).fill(false));
    setClicked2(Array(data.length).fill(false));
    setClicked3(Array(data.length).fill(false));
  }
  useEffect(() => {
    if (data.length) setAllStates();
    console.log(data);
  }, [data]);

  const handleOnclick = (rank, index, dish) => {
    switch (rank) {
      case "Rank 1":
        {
          let updatedClicks1 = clicked1.map((clicks, position) =>
            position === index ? true : false
          );
          setClicked1(updatedClicks1);
          dispatch({ type: "RANK_1", payload: dish });
        }
        break;
      case "Rank 2":
        {
          let updatedClicks2 = clicked2.map((clicks, position) =>
            position === index ? true : false
          );
          setClicked2(updatedClicks2);
          dispatch({ type: "RANK_2", payload: dish });
        }
        break;
      case "Rank 3":
        {
          let updatedClicks3 = clicked3.map((clicks, position) =>
            position === index ? true : false
          );
          setClicked3(updatedClicks3);
          dispatch({ type: "RANK_3", payload: dish });
        }
        break;
      default:
        break;
    }
  };
  const reselect = () => {
    dispatch({ type: "CLEAR_ALL" });
    setAllStates();
  };

  return (
    <>
      {ifSelected.length < 3 ? (
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
            <TableHead>
              <TableRow>
                <TableCell>Dish</TableCell>
                <TableCell align="right">Dish Name</TableCell>
                <TableCell align="right">Rank 1</TableCell>
                <TableCell align="right">Rank 2</TableCell>
                <TableCell align="right">Rank 3</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data.map((row, index) => (
                <TableRow
                  key={row.id}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell component="th" scope="row">
                    <img width={150} height={100} src={row.image} />
                  </TableCell>
                  <TableCell align="right">{row.dishName}</TableCell>
                  <TableCell align="right">
                    {clicked1[index] == false ? (
                      <>
                        <Button
                          variant="outlined"
                          onClick={() => handleOnclick("Rank 1", index, row)}
                        >
                          Select as Rank 1
                        </Button>
                      </>
                    ) : (
                      <Typography>Selected</Typography>
                    )}
                  </TableCell>
                  <TableCell align="right">
                    {clicked2[index] == false ? (
                      <Button
                        variant="outlined"
                        onClick={() => handleOnclick("Rank 2", index, row)}
                      >
                        Select as Rank 2
                      </Button>
                    ) : (
                      <Typography>Selected </Typography>
                    )}
                  </TableCell>
                  <TableCell align="right">
                    {clicked3[index] == false ? (
                      <>
                        <Button
                          variant="outlined"
                          onClick={() => handleOnclick("Rank 3", index, row)}
                        >
                          Select as Rank 3
                        </Button>
                        <Typography>{clicked3[index]}</Typography>
                      </>
                    ) : (
                      <Typography>Selected </Typography>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Typography>Selection Completed</Typography>
          <Typography component={Link} to="/rankings">
            See Rankings
          </Typography>
          <Button variant="contained" onClick={reselect}>
            Reselect
          </Button>
        </Box>
      )}
    </>
  );
}

export default Datatable;
